.details {
    position: relative;
    z-index: $zS;
    margin-top: 100px;
    height: 120px;
    width: calc(100% + 15px);
    &__box {
        padding: 0 60px 0 40px;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 120px;
        border-left: 8px solid $dark_green;
        border-radius: 3px;
        box-shadow: -3px 0px 7px 0 rgba(0, 0, 0, 0.15);
        background-color: $green;
        transform: scaleX(0);
        transform-origin: 0 0;
        @include vertical;
        text-align: left;
        &:after {
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            width: 20px;
            background-image: repeating-linear-gradient(45deg, rgba($black, 0.2), rgba($black, 0.2) 2px,  transparent 2px,transparent 6px);
            content: ''; } }
    &__circles {
        position: absolute;
        z-index: $zS; }
    &__circle-inner {
        @include vcenter;
        @include circle(12);
        background-color: $green;
        z-index: $z-2; }
    &__circle-outer {
        @include vcenter;
        @include circle(26);
        z-index: $z-1;
        background-color: rgba($green, 0.3); }
    .details__line-wrapper {
        position: absolute;
        right: 100%;
        height: 2px;
        width: 100%;
        // transform: rotate(3deg)
        transform-origin: 0 0;
        overflow: hidden; }
    .details__line {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: $green;
        transform: translateX(-105%);
        transform-origin: 0 0; }
    &__line {
        .is-active & {
            transform: scale(1); } }
    &__text {
        @include icenter;
        opacity: 0;
        font-size: 18px;
        line-height: 1.44;
        color: $white;
        letter-spacing: 0.03em; } }

